.Banner{
    display: flex;
    flex-direction: column;
    background-color: rgb(4,9,30,0.8);
    height: 100vh;
    width: 100%;
}
.container-heading{
    max-width: 1200px;
    margin:0 auto;
}
.Banner-heading{
    max-width: 800px;
    padding-top: 30vh;
    color:white;
}
.heading{
    font-size: 45px;
}
.sub-heading{
    color: #777;
    font-size: 14px;
    margin-bottom: 20px;
}
.start-button{
    background: #f7631b;
    line-height: 42px;
    padding-left: 30px;
    padding-right: 30px;
    border: none;
    color: #fff;
    display: inline-block;
    font-weight: 500;
    cursor: pointer;
}




#page-wrap {
    text-align: center;
    overflow: auto;
    top: 45%;
    position: relative;
  }
  
  .bm-item {
    display: inline-block;
  
    /* Our sidebar item styling */
    text-decoration: none;
    margin-bottom: 10px;
    color: #d1d1d1;
    transition: color 0.2s;
  }
  
  .bm-item:hover {
    color: white;
  }
  
  .bm-burger-button {
    position: fixed;
    width: 26px;
    height: 20px;
    right: 10%;
    top:65px;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background:black;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: #bdc3c7;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #000;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }